html,body{
    position: relative;
    height: 100%;
    background: darken(#ffffff,0);
}
header{
    top:0;
    position: fixed;
    height: 80px;
    width: 100%;
    background: black;
    z-index: 9999;
}
.my-navbar{
    height: 80px;
    width: 100%;
    background: none;
}
.my-navbar-left{
    padding-left: 0px;
    float: left;
}
.my-navbar-right{
    padding-right: 0px;
    float: right;
}
.my-navbar-right .my-navbar-nav{
    float: right;
}
.my-navbar-nav{
    display: block;
    height: 80px;
    width: 100px;
    background: black;
    float: left;
    color: white;
    text-align: center;
    padding-top: 30px;
}
/*.my-navbar-nav>span{
    
    margin-top: 30px;
}*/
.my-navbar-nav:hover{
    background: darken(#ffffff,75);
    border-bottom: 5px solid white;
}
.my-navbar-nav-1-content,
.my-navbar-nav-2-content,
.my-navbar-nav-3-content,
.my-navbar-nav-4-content,
.my-navbar-nav-5-content{
    height:300px;
    width:800px;
    background: darken(#ffffff,75);
    display: none;
    position:absolute;
    margin-top: 30px;
    left: 0px;
}
#my-navbar-nav-1:hover .my-navbar-nav-1-content{
    display: block;
}
#my-navbar-nav-2:hover .my-navbar-nav-2-content{
    display: block;
}
#my-navbar-nav-3:hover .my-navbar-nav-3-content{
    display: block;
}
#my-navbar-nav-4:hover .my-navbar-nav-4-content{
    display: block;
}
.my-navbar-nav-content-element{
    height: 100%;
    overflow: hidden;
}
.my-navbar-nav-content-element > h5{
    font-size: 14px;
    color: white;
    text-align: left;
    font-weight: bold;
}
.my-navbar-nav-content-element > ul{
    list-style-type: none;
    padding-left: 0px;
    padding-left: 5px;
}
.my-navbar-nav-content-element > ul li{
    text-align: left;
}
.my-navbar-nav-content-element > ul li a{
    color: white;
    font-size: 10px;
    text-align: left;
}
.my-navbar-nav-content-element > ul li a:hover{
    padding-left: 5px;
    text-decoration: none;
}
.my-navbar-nav-content-element-img{
    width: 160px;
    height: 80%;
    padding-top: 40px;
}




/*******************************BANNER*************************************/
.banner-container{
    width: 200%;
    height: 450px;
    background: #8a5858;
    margin-top: 80px;
    padding: 0px;
    overflow: hidden;
    float:left;
    //overflow-y: scroll;
    position: relative;//dropdown dose not work
    //position: fixed;//Show no side scroller & dropdown dose not work
}
.banner-image{
    height:450px;
    width:50%;
    //position: fixed;
    top: 80px;
    float: left;//crucial , if not given shows side scroll-bar
}
$animation-duration: 20s;
$animation-delay: 5s;
$animation-direction: alternate;
$animation-iteration-count: infinite;
.banner-image-4{
    -webkit-animation-name: banner-slider-image-4; /* Chrome, Safari, Opera */
    -webkit-animation-duration: 2s; /* Chrome, Safari, Opera */
    animation-name: banner-slider-image-4;
    animation-duration: $animation-duration;
    animation-delay: $animation-delay;
    //animation-direction: $animation-direction;
    animation-iteration-count: $animation-iteration-count;
}
.banner-image-3{
    -webkit-animation-name: banner-slider-image-3; /* Chrome, Safari, Opera */
    -webkit-animation-duration: 2s; /* Chrome, Safari, Opera */
    animation-name: banner-slider-image-3;
    animation-duration: $animation-duration;
    animation-delay: $animation-delay;
    //animation-direction: $animation-direction;
    animation-iteration-count: $animation-iteration-count;
}
.banner-image-2{
    -webkit-animation-name: banner-slider-image-2; /* Chrome, Safari, Opera */
    -webkit-animation-duration: $animation-duration; /* Chrome, Safari, Opera */
    animation-name: banner-slider-image-2;
    animation-duration: $animation-duration;
    animation-delay: $animation-delay;
    //animation-direction: $animation-direction;
    animation-iteration-count: $animation-iteration-count;
}
.banner-image-1{
    -webkit-animation-name: banner-slider-image-1; /* Chrome, Safari, Opera */
    -webkit-animation-duration: 2s; /* Chrome, Safari, Opera */
    animation-name: banner-slider-image-1;
    animation-duration: $animation-duration;
    animation-delay: $animation-delay;
    //animation-direction: $animation-direction;
    animation-iteration-count: $animation-iteration-count;
}

/*@-webkit-keyframes banner-slider-controller {
    0%   {margin-left:-100%;}
    25%  {margin-left:-200%;}
    50%  {margin-left:-300%;}
    75%  {margin-left:-400%;}
    100% {margin-left:-500%;}
}*/
@keyframes banner-slider-image-4 {
    0%   {margin-left:0%;}
    25%  {margin-left:-100%;}
    50%  {margin-left:-100%;}
    75%  {margin-left:-100%;}
    100% {margin-left:-100%;}
}
@keyframes banner-slider-image-3 {
    0%   {margin-left:0%;}
    25%  {margin-left:0%;}
    50%  {margin-left:-100%;}
    75%  {margin-left:-100%;}
    100% {margin-left:-100%;}
}
@keyframes banner-slider-image-2 {
    0%   {margin-left:0%;}
    25%  {margin-left:0%;}
    50%  {margin-left:0%;}
    75%  {margin-left:-100%;}
    100% {margin-left:-100%;}
}
@keyframes banner-slider-image-1 {
    0%   {margin-left:0%;}
    25%  {margin-left:0%;}
    50%  {margin-left:0%;}
    75%  {margin-left:0%;}
    100% {margin-left:0%;}
}



/*******************************Slider**************************************/
.slider-container{
    padding: 0px;
    position:relative;
}
.slider{
    width: 100%;
    position: relative;
    height: 500px;
    margin-top: 530px;
}
.slider-nav{
    width: 100%;
    background: darken(#ffffff,4);
    height: 50px;
}
.slider-nav-header{
    background: none;
    height: 100%;
    padding-top: 9px;
}
.slider-nav-header span{
    font-size: 24px;
    font-weight: bold;
    color: darkblue;
}
.slider-nav-navigation button{
    float: right;
    font-size: 10px;
    border-radius: 50%;
    color: white;
    width: 40px;
    height: 40px;
    padding-left: 8px;
    margin-top: 5px;
    margin-right: 10px;
    opacity: 0.3;
}
.slider-nav-navigation button:hover{
    opacity: 1;
}
.slider-element-container{
    width:100%;
    height: 440px;
    overflow: hidden;
}
.slider-element{
    margin-top: 15px;
    height: 420px;
    float: left;
    width: 25%;
}
.slider-element-header{
    height: 200px;
    width: 100%;
    position: relative;
    background: none;
}
.slider-element-header-discount{
    background: red;
    height: 50px;
    width: 50px;
    color: white;
    border-radius: 50%;
    position: absolute;
    right: 0px;
    padding-top: 17px;
    font-size: 12px;
    font-weight: bold;
    text-align: center;
    -ms-transform: rotate(15deg); /* IE 9 */
    -webkit-transform: rotate(15deg); /* Chrome, Safari, Opera */
    transform: rotate(15deg);
}
.slider-element-header-image{
    height: 100%;
    width: 100%;
}
.slider-image{
    height: 100%;
    width: 90%;
    padding-left: 5%;
    padding-right: 5%;
}
//slider content
.slider-element-content{
    height: 200px;
}
.slider-element-content-title{
    font-size: 15px;
    font-weight: bold;
    color: blueviolet;
    height: 50px;
    padding-left: 10px;
}
.slider-element-content-detail{
    height: 150px;
    padding-top: 5px;
    font-size: 12px;
    font-family: arial;
    color: black;
    padding-left : 20px;
}
//slider footer
.slider-element-footer{
    height: 20px;
    background: white;
    color: darkblue;
}
.slider-element-footer-rating{
    width: 50%;
    float: left;
    text-align: center;
}
.slider-element-footer-price{
    width: 50%;
    float: right;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
}

/*******************************FOOTER*************************************/
footer{
    position:fixed;
    bottom:0px;
    height:200px;
    width:100%;
    background: black;
}


/******************************DASHBOARD***********************************/
$dashboard-header-height: 80px;
html,body{
    position: relative;
    height: 100%;
    background: darken(#ffffff,0);
}
.dashboard-app-header{
    height:40px;
    width:100%;
    background:#363A41;
    color: white;
    position: fixed;
    z-index: 9999;
}
#dashboard-app-layout .container-fluid{
    padding:0px;
}
#dashboard-app-layout .col-sm-2{
    padding:0px;
}
#dashboard-app-layout .dashboard-nav{
    height: 100%;
    background: #363A41;
    overflow: hidden;
    text-align: center;
    position: fixed;
    width: 16.67%;
    min-width: 128px;
    z-index: 9999;
}
#dashboard-app-layout .dashboard-nav-header-left{
    line-height: 2em;
    font-size: 1.2em;
    color: white;
    display: block;
    height: 40px;
    padding-top: 3px;
    text-align: center;
}
.dashboard-nav-right{
    padding: 0px;
}
#dashboard-app-layout .dashboard-nav-header-right{
    float: right;
    width: 100%;
    padding-left: 10px;
    height: 40px;
    text-align: center;
    //margin-top: -3px;
}
#dashboard-app-layout .dashboard-nav-header-right ul{
    list-style-type: none;
}
#dashboard-app-layout .dashboard-nav-header-right li{
    display: block;
    width: 10%;
    height: 40px;
    float: right;
}
#dashboard-app-layout .dashboard-nav-header-right a{
    text-decoration: none;
    color: white;
    font-size: 12px;
    display: block;
    height : 40px;
    padding-top: 10px;
}
#dashboard-app-layout .dashboard-nav-header-right a:hover{
    cursor: pointer;
    background: black;
}
/*******************Dash Biard Nav***********************/
#dashboard-app-layout .dashboard-nav ul{
    list-style-type: none;
    padding:0px;
}
#dashboard-app-layout .dashboard-nav ul li{
    background: #363A41;
    //padding-top: 10px;
    //padding-bottom: 10px;
    height: 38px; 
    width: 100%;
    display: block;
    text-align: left;
}
#dashboard-app-layout .dashboard-nav ul li:hover{
    cursor: pointer;
    border-right: 4px solid darken(#363A41, 25);
    background: darken(#363A41, 5);
}
#dashboard-app-layout .dashboard-nav ul li a{
    text-decoration: none;
    font-size: 12px;
    color: white;
    padding-left: 10px;
    height: 38px; 
    padding-top: 10px;
    display: block;
}
#dashboard-app-layout .fa{
    color: white;
    padding-left: 24px;
    padding-right: 10px;
}
#dashboard-app-layout .dashboard-nav ul ul{
    //left: 224px;
    margin-left:16.67%;//100% if position "absolute"
    position: fixed;//absolute
    margin-top: 40px;
    display: none;
    width:200px;
    z-index: 99999;
}
//for vertical nav bar side drop down effect
#dashboard-app-layout .product-options{
    top: 0px;
}
#dashboard-app-layout .dashboard-nav ul li:nth-child(1):hover .product-options{
    display:block;
}
#dashboard-app-layout .category-options{
    top: 38px;
}
#dashboard-app-layout .dashboard-nav ul li:nth-child(2):hover .category-options{
    display:block;
}
#dashboard-app-layout .vendor-options{
    top: 190px;
}
#dashboard-app-layout .dashboard-nav ul li:nth-child(6):hover .vendor-options{
    display:block;
}
/*************************Dash BOard Content********************************/
.dashboard-content{
    margin-top: 40px;
}
#dashboard-app-layout .table {
    font-size: 12px;
    text-align: center;
}
#dashboard-app-layout .table th{
    font-size: 13px;
    font-weight: bold;
    text-align: center;
}
#dashboard-app-layout .table td{
    padding-top: 15px;
}
#dashboard-app-layout .table .btn{
    font-size: 10px;
}
